

























































































































































































import Vue from 'vue';

export default Vue.extend({
  name: 'agenda',
  props: {
    type: {
      type: String as () => 'dominant-bonus' | 'policy-bonus' | 'party-bonus',
    },
    id: {
      type: String as () => string | undefined,
    },
  },
  computed: {
    getClass(): string {
      switch (this.type) {
      case 'dominant-bonus':
        return 'dominant-party-bonus';
      case 'party-bonus':
        return 'party-bonus';
      }
      return '';
    },
  },
});

