





















import Vue from 'vue';
import {SimpleGameModel} from '@/common/models/SimpleGameModel';
import {Phase} from '@/common/Phase';

export default Vue.extend({
  name: 'GameOverview',
  data() {
    return {
    };
  },
  props: {
    status: {
      type: String,
    },
    game: {
      type: Object as () => SimpleGameModel | undefined,
    },
    id: {
      type: String,
    },
  },
  methods: {
    isRunning(): boolean {
      return this.game?.phase !== Phase.END;
    },
  },
});
